<template>
    <div>
        <div class="guanggao" v-on:click="shouye">
          <img src="../../assets/images/shanping.jpg" >
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import router from '../../router'
    export default {
      name:'guanggao',
      methods:{
        shouye(){
          router.push('/')
        }
      }
    }
</script>

<style>
  .guanggao{
    width: 1080px;
    height: 1920px;
    background: #ffffff;
    position: fixed;
    top: 0;
    left: 50%;
    margin-left: -540px;
    z-index: 500;
  }
</style>



