<template>
  <div>
    <Navbar></Navbar>
    <div class="container">

      <h1>您好！  欢迎向我们提出问题！</h1>
      <div class="label">分类 </div>
      <select name="" v-model="type">
        <option value="0" >建议</option>
        <option value="1" >投诉</option>
      </select>
      <div class="label">姓名 Name</div>
      <input type="text" v-model="suggested">
      <div class="label">电话 Phone</div>
      <input type="number" v-model="phone" style="width: 500px">
      <div class="label">信息</div>
      <textarea name="" v-model="content" cols="80" rows="12"></textarea>
      <button @click="send()">提交信息</button>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import Navbar from '@/components/common/Navbar'
  import router from '../../router'
  import store from '@/vuex/store'
  export  default {
    name:'yijiantousu',
    data(){
      return{
        type:'0',
        code:'',
        content:'',
        phone:'',
        suggested:'',
        url:this.$store.state.url
      }
    },
    components:{
      Navbar
    },
    store,
    methods:{

      getcode(){
        const self=this
        $.ajax({
          type: 'get',
          url: self.url+'../zhcomplaint/advice/creatCode',
          jsonp: 'callbackparam',
          jsonpCallback: "jsonpCallback",
          dataType: 'jsonp',
          success: function (data) {
            self.code=data
          }
        })
      },
      send() {
        console.log(this.type,this.content,this.phone,this.suggested,this.code)
        if(this.content==''||this.phone==''||this.suggested==''||this.type==''){alert('请输入完成！');return false}
        const self=this;
        $.ajax({
          type: 'get',
          url: self.url + '../zhcomplaint/advice/saveOrUpdate',
          data: {
            'type': self.type,
            'suggested': self.suggested,
            'phone': self.phone,
            'content': self.content,
            'code': self.code
          },
          jsonp: 'callbackparam',
          jsonpCallback: "jsonpCallback",
          dataType: 'jsonp',
          success: function (data) {
            console.log(data)
            if(data==true){
              alert('提交成功！')
            }else{alert('提交失败！') ;return false}
          }
        })
      },
    },
    created(){
      this.getcode()
    }
  }

