<template>
  <div id="app" v-on:click="guanggao()">
    <headtop></headtop>
    <div class="content">
      <transition name="slide-fade"><router-view></router-view></transition>
    </div>
    <div class="background"><img src="../src/assets/images/background.jpg" alt=""></div>
    <footBottom></footBottom>
  </div>
</template>

<script>
  import  headtop from '@/components/common/Head.vue'
  import footBottom from '@/components/common/Foot.vue'
  import router from './router'
  import '@/assets/css/main.css'
export default {
  name: 'app',
  data(){
    return {
      gIndex:5,
    }
  },
  components:{
    headtop,
    footBottom
  },
  methods:{
    guanggao(){
      for(var i = 1; i < 1000; i++) {
        if(clearTimeout(i)){
          return false
        }
      }
      const guanggao=setTimeout(function () {
        router.push('/guanggao')
      },300000)
    }
  }
  ,
  created(){
    this.guanggao()
  }
}
</script>

<style>
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .1s ease;
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    opacity: 0;
  }
#app {
  font-family: Arial,Helvetica,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 1080px;height: 1920px;
  margin:  0 auto;
  position: relative;
}
.background{position: absolute;right: 0%;
  top:487px;}

hr{border:none;border-bottom:1px solid #cacaca  ;
}
::-webkit-scrollbar{width: 0;}
  body{
    padding: 0 ;margin: 0;

  }
.content-margin{
    margin-top: 25px;
  }
  .pull-left{float: left}
  .pull-right{float: right}
  .overflow{overflow: hidden}
  .content{width: 864px;margin: 0 auto;height: 1064px;margin-top: 104px ;overflow: scroll}
  ul{overflow:hidden}
  ul li{float: left}
</style>
<style >
</style>



