<template>
  <div>
    <Navbar></Navbar>
    <div class="container">
      <ul>
        <li v-for="list in lists">
          <div class="left-img"><img :src="url+list.tp" width="100%" alt=""></div>
          <div class="right-content">
            <h4>{{ list.mc }}</h4>
            <p>{{ list.js }}</p>
            <button class="btn" @click="tiaozhuan(list.id)">查看更多</button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import router from '../../router'
  import store from '@/vuex/store'
  export  default {
    name:"xianlutuijian",
    data() {
      return {
        url:store.state.url,
        lists:[]
      }
    },
    components:{
      Navbar
    },
    store,
    methods:{
      creat(){
        var self=this
        $.ajax({
          type: 'get',
          url: this.url + 'resources/tjlx/list',
          data: {
            'page': 1, 'size': 100
          },
          dataType: 'jsonp',
          success: function (data) {
            console.log(data)
            self.lists=data.data.content
          }
        })
      },
      tiaozhuan(id){
        this.$store.state.zhanyuanid=id;
        router.push('/xianlutuijianxiangqing')
      }
    },
    created(){
      this.creat()
    }
  }
</script>

<style scoped>
  .container ul{width: 100%}
  .container ul li{height: 252px;width: 100% ;overflow: hidden;margin-top: 56px}
  .container ul li >div{float: left}
  .left-img{width: 600px}
  .right-content{width: 242px;padding-left: 22px;text-align: left;position: relative;height: 252px}
  .right-content h4{font-size: 23px;margin-top: 40px;color: #4b4a4f;font-weight: 500}
  .right-content p{
    margin-top: 10px;
    font-size: 16px;
    line-height: 24px;
    color: #b4b3b8;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
  }
  .right-content button{
    position: absolute;
    left:22px;bottom: 30px;
    width: 88px;
    height: 32px;
    line-height: 33px;
    font-size: 14px;
    border: none;
    color: #ffffff;
    background: #489008;
  }
</style>



