<template>
    <div class="zhanyuan" id="zhuanyuanlei">
      <Navbar></Navbar>
      <div class="content-margin overflow">
          <div class="left pull-left">
            <ul>
              <li v-for="list in lists"  :class="{active: active == list.name}"   @click="changeid(list.id);selected(list.name)">{{ list.name }}</li>
            </ul>
          </div>
          <div class="right pull-right">
            <ul>
              <li v-for="listR in listRight" @click="zygo(listR.id);">
                  <img :src="$store.state.url+listR.qzl" width="100%" alt="">
                  <span>{{ listR.mc }}</span>
              </li>
            </ul>
          </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import Navbar from '../common/Navbar.vue'
    import router from '../../router'
    import store from '@/vuex/store'
    export  default {
      name:'Zhanyuanlei',
      data(){
        return {
          rightId:'',
          lists:[],
          listRight:[],
          active:'河南展园'
        }
      }
      ,
      components:{
        Navbar
      },
      store,
      methods:{
        selected: function(list) {
          this.active = list
        },
        changeid(i){
          this.id=i
          this.listRights(this.id)
        },
        listRights:function (id) {
          var self=this
          $.ajax({
            type: 'get',
            url: self.$store.state.url+'resources/yqdlGcyq/findGardenList',
            data: {'gardenTypeId': id, 'page': 1, 'size': 100},
            dataType: 'jsonp',
            success: function (data) {
              console.log(data)
                self.listRight=data.data.content
            }
          })
        },
        zygo:function (id) {
           this.$store.commit('zhanyuanxq',id)
          router.push('/zhanyuanxq')
        }

      },
      created(){
        var self=this
        $.ajax({
          type: 'get',
          url: self.$store.state.url+'resources/gardenType/list',
          dataType: 'jsonp',
          success: function (data) {
//            console.log(data)
            self.lists=data.data
            self.listRights(self.lists[0].id)
          }
        })
      }
    }
</script>

<style scoped>

  .left ul{width: 150px}
  .left ul li{height: 56px;width:150px;border-bottom:1px solid #e9e8ee;background: #f0eff5;line-height: 57px;font-size: 20px;color:#939395}
  .active{color:#ffffff!important;background: #489008!important;}
  .right{width: 710px}
  .right ul{width: 100%;height: 837px;overflow: scroll}
  .right ul li{width: 217px;margin: 0 9px;float: left}
  .right ul li span{font-size: 22px;line-height: 64px;margin-bottom: 11px}
  ::-webkit-scrollbar{width:0px}
</style>



