<template>
  <div>
    <Navbar></Navbar>
    <div class="container">
      <ul>
        <li @click="open(e.id)" v-for="(e,i) in lists">
          <div class="list">
            <div class="date">
              <div >{{new Date(parseInt(e.modifiedDate) * 1000).getDate()}}  </div> <span > {{(new Date(parseInt(e.modifiedDate) * 1000).getYear())+'-'+(new Date(parseInt(e.modifiedDate) * 1000).getMonth()+1)}}</span>
            </div>
            <div class="list-content">
              <h4>{{ e.mc }}</h4>
              <p><span><img src="../../assets/images/bendi.jpg" alt=""> 办公室</span></p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import store from '@/vuex/store'
  import router from '../../router'
  export  default {
    data() {
      return {
          lists:{},
        url:this.$store.state.url
      }
    },
    components: {
      Navbar
    },
    store,
    methods:{
      open(id){
        this.$store.state.zhanyuanid=id
          router.push('/shengtaigushixiangqing')
      },
    },
    created(){
      var self=this
      $.ajax({
        type: 'get',
        url: store.state.url + 'resources/ybzx/list',
        data: {'lx': 5, 'page': 1, 'size': 10},
        dataType: 'jsonp',
        success: function (data) {
          console.log(data)
          self.lists=data.data.content
        }
      })
    }
  }
</script>

<style scoped>
  ul,li,.list{width: 100%;text-align: left}
  li{height:124px;border-bottom: 1px solid #eaeaea;}
  .list{   margin-top: 31px;height: 64px;}
  .date{

    text-align: center;
    width: 60px;
    height: 62px;
    background: #f0eff5;
    color: #747474;
    border:1px solid #5da925;
  }
  .date div{
    font-size: 28px;
    font-weight: bold;
    line-height: 38px;
    height: 38px;
    background: #60ad2d;
    color:#fff
  }
  .date span{font-size: 10px;  margin-top: 10px;line-height: 25px}
  .list{overflow: hidden}
  .list>div{float: left}
  .list-content{height: 100%;width: 770px;  padding-left: 22px;  }
  h4{font-size: 20px;    margin-top: 3px;  margin-bottom: 12px;}
  .list-content p {font-size: 14px}
  .list-content p span img{margin-bottom: -4px;  margin-left: 6px;  }

</style>



