<template>
  <div class="navbar">
    <span><router-link to="/">首页 • </router-link> </span><span style="padding-left: 8px;">{{ $store.state.nav }}</span>
    <div class="home" @click="home()">
      <img src="../../assets/images/home.png" alt="">
    </div>
    <div class="his" @click="his()">
      <img src="../../assets/images/his.png" alt="">
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import store from '@/vuex/store'
  import router from '../../router'
   export default {
     name:'Navbar',
     data (){
         return {
         }
     },
     store,
     methods:{
       his(){
         router.go(-1)
       },
       home(){
         router.push('/')
       }
     }
   }
</script>

<style>
  #app{position: relative}
  .navbar{text-align: left;font-size: 16px;color:#9c9c9c}
  .navbar span:nth-child(1){
    display: inline-block;
    padding-left: 24px;
    background: url('../../assets/images/home.jpg') no-repeat left;
    background-position-y: 1px;
  }
  .his{    position: absolute;
    bottom: 500px;
    right: 30px;}
  .home{    position: absolute;
    bottom: 380px;
    right: 30px;}
</style>



