import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/shouye/Hello'
import Test from '@/components/test'
import Zhanyuanlei from '@/components/zhanyuan/Zhanyuanlei'
import Zhanyuanxq from '@/components/zhanyuan/Zhanyuanxiangqing'
import Shengtaigushilei from '@/components/shengtaigushi/Shengtaigushilei'
import Shengtaigushixiangqing from '@/components/shengtaigushi/Shengtaigushixiangqing'
import Yijiantousu from '@/components/yijiantousu/Yijiantousu'
import Yuanbozhixunlei from '@/components/yuanbozhixun/Yuanbozhixunlei'
import Yuanboxiangqing from '@/components/yuanbozhixun/Yuanboxiangqing'
import Yuanqudaolan from '@/components/yuanqudaolan/Yuanqudaolan'
import Zhiwulei from '@/components/zhiwu/Zhiwulei'
import Zhiwuxiangqing from '@/components/zhiwu/Zhiwuxiangqing'
import Zhiwuxiangqinglei from '@/components/zhiwu/Zhiwuxiangqinglei'
import Xianlutuijianlei from '@/components/xianlutuijian/xianlutuijianlei'
import Xianlutuijianxiangqing from '@/components/xianlutuijian/Xianlutuijianxiangqing'
import Tingchexitong from '@/components/tingchexitong/Tingchexitong'
import Jingjibaojin from '@/components/jingjibaojin/Jingjibaojin'
import Guanggao from '@/components/guanggao/Guanggao'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path:'/test',
      name:'Test',
      component:Test
    }
    ,
    {
      path:'/zhanyuanlei',
      name:'Zhanyuanlei',
      component:Zhanyuanlei
    }
    ,
    {
      path:'/zhanyuanxq',
      name:'Zhanyuanxq',
      component:Zhanyuanxq
    }
    ,
    {
      path:'/shengtaigushilei',
      name:'shengtaigushilei',
      component:Shengtaigushilei
    }
    ,
    {
      path:'/shengtaigushixiangqing',
      name:'shengtaigushixiangqing',
      component:Shengtaigushixiangqing
    }
    ,
    {
      path:'/yijiantousu',
      name:'yijiantousu',
      component:Yijiantousu
    }
    ,
    {
      path:'/yuanbozhixunlei',
      name:'yuanbozhixunlei',
      component:Yuanbozhixunlei
    }
    ,
    {
      path:'/yuanboxiangqing',
      name:'yuanboxiangqing',
      component:Yuanboxiangqing
    }
    ,
    {
      path:'/yuanqudaolan',
      name:'yuanqudaolan',
      component:Yuanqudaolan
    }
    ,
    {
      path:'/zhiwulei',
      name:'zhiwulei',
      component:Zhiwulei
    }
    ,
    {
      path:'/zhiwuxiangqinglei',
      name:'zhiwuxiangqinglei',
      component:Zhiwuxiangqinglei
    }
    ,
    {
      path:'/zhiwuxiangqing',
      name:'zhiwuxiangqing',
      component:Zhiwuxiangqing
    }
    ,
    {
      path:'/xianlutuijianlei',
      name:'xianlutuijianlei',
      component:Xianlutuijianlei
    }
    ,
    {
      path:'/xianlutuijianxiangqing',
      name:'xianlutuijianxiangqing',
      component:Xianlutuijianxiangqing
    }
    ,
    {
      path:'/tingchexitong',
      name:'tingchexitong',
      component:Tingchexitong
    }
    ,
    {
      path:'/jingjibaojin',
      name:'jingjibaojin',
      component:Jingjibaojin
    }
    ,
    {
      path:'/guanggao',
      name:'guanggao',
      component:Guanggao
    }
  ]
})



