<template>
    <div>
      <Navbar></Navbar>
      <div class="container">
          <div class="pic">
            <img :src="imgs[i]"/></div>
          <div class="button">
              <ul>
              <li @click="changei(0)"><img src="../../assets/images/button1.png" alt=""></li>
              <li @click="changei(1)"><img src="../../assets/images/button2.png" alt=""></li>
              <li @click="changei(2)"><img src="../../assets/images/button3.png" alt=""></li>
              <li @click="changei(3)"><img src="../../assets/images/button4.png" alt=""></li>
              <li @click="changei(4)"><img src="../../assets/images/button5.png" alt=""></li>
              <li @click="changei(5)"><img src="../../assets/images/button6.png" alt=""></li>
            </ul>
          </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import store from '@/vuex/store'
  export  default {
    data(){
      return {
        i:0,
        imgs:[ require('../../assets/images/daolan1.jpg'),require('../../assets/images/daolan2.jpg'),
          require('../../assets/images/daolan3.jpg'),require('../../assets/images/daolan4.jpg'),
          require('../../assets/images/daolan5.jpg'),require('../../assets/images/daolan6.jpg')
        ],
      }
    },
    components:{
      Navbar
    },
    methods:{
      changei(n){
        this.i=n
      }
    },
  store
  }
</script>

<style scoped>
  .pic {width: 864px;height: 704px;margin-top: 25px}
  .button{width: 100%;margin-top: 22px}
  .button ul{width: 100%}
  .button ul li{    margin: 0 1px;}
</style>



