<template>
  <div class="container">
    <Navbar></Navbar>
    <h1>{{ $store.state.lists.name }}</h1>
    <p class="explain"><span>20170707</span> 来源：<span>郑州园博会</span></p>
    <hr>
    <swiper :options="swiperOption" ref="mySwiperA" id="pic">
      <!-- 幻灯内容 -->
      <swiper-slide v-for="(e,i) in src" class="swiper-content">
        <img  :src="e" alt="" v-on:error="errors(i)">
      </swiper-slide>
      <!-- 以下控件元素均为可选 -->
      <div class="swiper-pagination" slot="pagination"></div>
      <!--<div class="swiper-scrollbar" style="display: none"  slot="scrollbar"></div>-->
    </swiper>
    <!--<img :src=" $store.state.url+lists.img "  alt="">-->
    <div class="text" id="text"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import store from '@/vuex/store'
  export default{
    name:'zhanyuanxiangqing',
    data (){
      return {
        rIndex:[],
        lists: {
          name:this.$store.state.lists
        },
        src:[
        ],
        srcs:[],
        swiperOption: {
          // 所有配置均为可选（同Swiper配置）
          notNextTick: true,
          autoplay: 3000,
          grabCursor : true,
          setWrapperSize :true,
//          autoHeight: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
//          prevButton:'.swiper-bu.tton-prev',
//          nextButton:'.swiper-button-next',
//          scrollbar:'.swiper-scrollbar',
//          mousewheelControl : true,
          observeParents:true,
          onTransitionStart(swiper){
//            console.log(swiper)
          }
        }
      }
    },
    components:{
      Navbar
    },
    store,
    methods:{
      errors(n){

        this.rIndex.push(n)
      }
    },
    created(){
//      console.log(this.src)
      var self=this
      $.ajax({
        type: 'get',
        url: store.state.url+'../zhcomplaint/changGuo/changGuanInfo',
        data: {'id': store.state.zhanyuanid},
        jsonp: 'callbackparam',
        jsonpCallback: "jsonpCallback",
        dataType: 'jsonp',
        success: function (data) {
          document.getElementById('text').innerHTML=data.fwb
          self.src=[
            './static/changguan/'+data.name+'/1.jpg',
            './static/changguan/'+data.name+'/2.jpg',
            './static/changguan/'+data.name+'/3.jpg',
            './static/changguan/'+data.name+'/4.jpg',
            './static/changguan/'+data.name+'/5.jpg',
            './static/changguan/'+data.name+'/6.jpg',
            './static/changguan/'+data.name+'/7.jpg',
            './static/changguan/'+data.name+'/8.jpg',]
        }
      })
    },
    computed:{
      swiper() {
        return this.$refs.mySwiperA.swiper
      }

    },

  }
</script>
<style scoped>
  .container h1{
    font-size: 26px;
  }
  .container img{width: 516px;margin: 40px auto }
  .explain{color:#b6b6b6;font-size: 10px;line-height: 51px}

  .text {font-size: 14px;line-height: 28px;text-align: left;margin: 14px 0;text-indent: 38px}
  .text p{padding: 14px 0}
  .text :nth-child(1){text-align: center}
</style>



