<template>
  <div>
    <Navbar></Navbar>
    <div class="container">
      <ul>
        <li v-for="list in lists">
          <div class="kuai">
            <div>
              <img :src="url+list.tp" width="186" height="270" alt="">
              <div class="date">
                  <div>
                      {{ new Date(parseInt(list.modifiedDate) * 1000).getDate()}}
                  </div>
                  <span> {{ new Date(parseInt(list.modifiedDate) * 1000).getYear()+'-'+ (new Date(parseInt(list.modifiedDate) * 1000).getMonth()+1)}}</span>
              </div>
            </div>
            <div class="kuai-text">
              <h4>{{ list.mc }}</h4>
              <p>{{ list.js }}</p>
              <router-link to="/yuanboxiangqing"><button @click="$store.commit('change','资讯列表');$store.commit('change2','园博资讯');$store.commit('zhanyuanxq',list.id)" >查看更多</button></router-link>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import store from '@/vuex/store'
  export  default {
    name:'yuanbozhixunlei',
    data(){
      return {
        lists:[],
        url:store.state.url
      }
    },
    components:{
      Navbar
    },
    store,
    created(){
      var self=this
      $.ajax({
        type: 'get',
        url: store.state.url + 'resources/ybzx/list',
        data: {'lx': 6, 'page': 1, 'size': 10},
        dataType: 'jsonp',
        success: function (data) {
          console.log(data)
          self.lists=data.data.content
        }
      })
    }
  }
</script>

<style scoped>
  .container{height: 900px;width:864px;overflow: scroll;margin-bottom: 138px;overflow-x: hidden}
  .container ul{width: 864px;text-align: left}
  .container ul li{width: 432px;margin: 20px 0;height: 270px}
  .kuai{width: 426px;margin: 0 auto ;overflow: hidden;background: #f0eff5}
  .kuai >div{ float: left;}
  .kuai-text{width: 218px;padding-left: 22px;height: 270px}
  .kuai-text h4{font-size: 18px;margin: 20px 0 18px 0;line-height: 26px;height: 51px;width: 170px;display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;}
  .kuai-text p{width: 186px;height: 100px;font-size: 14px;line-height: 26px;color:#b4b3b8;display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;}
  .kuai-text button {margin-top: 30px;width: 88px;height: 32px;line-height: 33px;font-size: 14px;border: none;color:#ffffff;background: #489008}
  .kuai div:nth-child(1){position: relative}
  .date{text-align: center;position: absolute;width: 81px;height: 72px;right: 0 ;top: 12px;background: #f0eff5;color:#747474}
  .date div{font-size: 28px;font-weight: bold;line-height: 28px;margin-top: 18px}
  .date span{font-size: 10px;margin-top: 10px}
</style>



