const shengtai={
  "content": [
    {
      "tp": "1/1.jpg",
      "mc": "盲猴",
      "js": "<p>&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; 3月5日，一部由河南本土团队创作的纪录片《盲猴》登陆美国国家地理频道，成为我省首部登上国际主流纪录片媒体平台的纪录片。“这不仅是对我们作品的肯定，也反映出西方观众对河南风土人情的喜爱。”该片拍摄团队、河南华之杰文化传播有限公司负责人王崇茂说。</p><p><strong>&nbsp;&nbsp;&nbsp;&nbsp; 视角：盲猴生命凸显自然生态</strong></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 据了解，《盲猴》历时3年完成，这部50分钟的自然类纪录片通过生动的故事、唯美的画面、感人的细节真实记录了一个双目失明的小盲猴在恶劣的自然环境中艰难成长的故事。该片导演海金星介绍，片子在济源市五龙口镇拍摄，那里生活着许多野生猕猴。一个偶然的机会，拍摄团队在五龙口发现了一只双目失明的小盲猴。对于猴子来说，没有视觉就无法奔走跳跃，只能依靠母猴喂养。</p><p></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;随着盲猴的长大，母猴从未放弃它。“出于本能，我们开始跟踪拍摄这只小盲猴，一是盲猴出现的几率极小，我们想与它一起面对未知的生命历程；二是想通过这群猴子，展现我们太行山优美的自然风光，也让观众在这部作品中感受到生命的坚韧与顽强。”海金星说。</p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 在摄制组的镜头中，小盲猴一天天长大，开始学会爬行、上树，学会寻找食物。当地最高海拔1800米，冬季最低温度达零下10摄氏度，小盲猴不仅要抵御寒冷和食物匮乏，更要时刻面临母猴再次怀孕的不利消息。“按照猴子的习性，如果猴妈妈再次怀孕，她很可能会放弃小盲猴让它独自生活，小盲猴未来的命运将会怎样，正是这部纪录片最大的悬念所在。”该片另一位导演叶静说。</p><p><strong>&nbsp;&nbsp;&nbsp; 意外：片花获得业内青睐</strong></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;2014年10月，华之杰制作团队将拍摄内容制作成一部5分钟的片花，报送中国最大的国际纪录片节——广州国际纪录片节。原本就是想展示一下，没想到小盲猴的故事引发了众多国际买家以及纪录片专家的关注，其中就包括曾经以影片《罩袍下的世界》获得艾美奖的英国著名纪录片制作人史蒂芬赛登伯格。在史蒂芬赛登伯格先生的引荐下，来自河南的团队与西班牙制作团队共同完成了《盲猴》的拍摄和制作。</p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 2016年9月，《盲猴》在四川“金熊猫”国际纪录片节上获得自然与环境类纪录片提名奖，评委评价说：“这个片子选材很特别，让人们在了解动物世界的同时，也告诉了我们同情和怜悯不是人类所独有的。”</p><p><strong>&nbsp;&nbsp;&nbsp; 领悟：河南故事的全球化表达</strong></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;尽管是一次意外之举，但也给拍摄团队带来深深的思考——如何用国际化的表述方式传播中国故事。</p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 在BBC工作多年的美国著名摄影师摄像米切尔法卡斯说，随着中国在国际舞台上大国地位的确立，纪录片日益成为展现中国软实力、塑造国家形象的优良载体。《舌尖上的中国》《超级工程》等吸引了大批国外观众。我们觉得以真实的影像记录中国的自然生态，展现不易看到的影像，都是为国家形象推广注入鲜活的元素。</p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 王崇茂认为，目前国家印发了《关于实施中华优秀传统文化传承发展工程的意见》，还接连出台一系列大力扶持国产纪录片创作的利好政策，为国产纪录片的创作与生产带来勃勃生机。此次《盲猴》的成功为河南纪录片国际合作提供了宝贵经验，坚定了用纪录片助力河南文化走出去的信心。</p>"
    }
  ]
}
const state={
  url:'http://zhihui.expo2017.net.cn/zzwhe/',
  str:'',
  zhanyuanid:'', //展园id
  nav:'',
  nav2:'',
  yydlImg:'',
  erjiid:'',
  sanjiid:'',
  shengtai: shengtai,
  lists:{},
  guanggaoIndex:0
}
const mutations={
  change(state,str){ //nav改变
    state.nav=str
  },
  change2(state,str){ //nav改变
    state.nav2=str
  },
  zhanyuanxq(state,id){
    state.zhanyuanid=id
  },
  yydl(state,src){
    state.yydlImg=src
    console.log(state)
  },
  localState:state=>localStorage.state=state
}
export default new Vuex.Store({
  state,
  mutations
})





