<template>
  <div class="container">
    <Navbar></Navbar>
    <h1>{{ list.mc }}</h1>
    <p class="explain"><span>{{ new Date(parseInt(list.modifiedDate) * 1000).toLocaleString().replace(/年|月/g, "-").replace(/日/g, " ")}} </span> 来源：<span>郑州园博会</span></p>
    <hr>
    <img :src=" url+list.tp "  alt="">
    <div class="text" id="text"></div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import store from '@/vuex/store'
  export default {
    name: 'zhanyuanxiangqing',
    data() {
      return {
        url:store.state.url,
        id:store.state.zhanyuanid,
        list:{}
      }
    },
    components: {
      Navbar
    },
    store,
    created(){
      var self=this
      $.ajax({
        type: 'get',
        url: self.url + 'resources/ybzx/details',
        data: {'id': self.id},
        dataType: 'jsonp',
        success: function (data) {
          self.list=data.data
          document.getElementById('text').innerHTML=data.data.xq
        }
      })
    }
  }
</script>

<style scoped>
  .container{height: 900px;width:864px;overflow: scroll;margin-bottom: 138px;overflow-x: hidden}
  .container h1{
    font-size: 26px;margin-top: 70px;
  }
  .container img{width: 516px;margin: 40px auto }
  .explain{color:#b6b6b6;font-size: 10px;line-height: 51px}
  hr{border:none;border-bottom:1px solid #cacaca  ;
  }
  .text {font-size: 14px;line-height: 28px;text-align: left;margin: 14px 0;text-indent: 38px}
  .text p{padding: 14px 0;text-align: justify}
</style>



