<template>
  <div class="container">
    <Navbar></Navbar>
    <h1>{{ lists.mc }}</h1>
    <p class="explain">
      <!--<span>{{ time }}</span>-->
      来源：<span>郑州园博会</span></p>
    <hr>
    <img :src=" $store.state.url+lists.tp " alt="">
    <div class="text" id="text"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import store from '@/vuex/store'

  export default {
    data() {
      return {
        url:this.$store.state.url,
        lists:{},
        id:store.state.erji,
      }
    },
    store,
    components: {
      Navbar
    },
    methods:{
      creat(){
        var self=this
        $.ajax({
          type: 'get',
          url: this.url + 'resources/zwbkZw/one',
          data: {'id': self.id},
          dataType: 'jsonp',
          success: function (data) {
            console.log(data)
            self.lists=data.data
            document.getElementById('text').innerHTML=data.data.js
          }
        })
      }
    },
    created(){
        this.creat()
    }
  }

</script>

<style scoped>
  .container h1 {
    font-size: 26px;
  }

  .container img {
    width: 516px;
    margin: 40px auto
  }

  .explain {
    color: #b6b6b6;
    font-size: 10px;
    line-height: 51px
  }

  .text {
    font-size: 14px;
    line-height: 28px;
    text-align: left;
    margin: 14px 0;
    text-indent: 38px
  }

  .text p {
    padding: 14px 0
  }

  .text :nth-child(1) {
    text-align: center
  }
</style>



