<template>
  <div>
    <Navbar></Navbar>
    <div class="container">
      <h4>各停车场车位剩余情况  <span class="pull-right">共剩余车位：239个 <img src="../../assets/images/refresh.jpg" alt=""></span></h4>
      <table>
        <tr v-for="(e,i) in arrs">
          <td class="td1">
            <div>郑州园博会北门停车场</div>
          </td>
          <td class="td2">
            <div class="td2-pro">
              <div class="progress">
                <span v-bind:class="[i%4==0?green:i%4==1?red:i%4==2?orange:blue]" v-bind:style="{width: (e.width+'%')}"></span>
              </div>
            </div>
          </td>
          <td class="td3">
            <div>{{ e.width }}/120</div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  export  default {
    name:'tingchexitong',
    data() {
      return {
        green:'green',
        red:'red',
        orange:'orange',
        blue:'blue',
        arrs:[{width:20},{width:40},{width:60},{width:80}]
      }
    },
    components:{
      Navbar
    }
  }
</script>

<style scoped>
  h4 span{font-size: 16px;color:#8d949a;margin-top: 12px}
  h4 img{margin-bottom: -3px;}
  .container{text-align: left}
  .container h4{
    font-weight: 500;  padding-left: 19px;  margin-top: 50px;font-size: 24px;
  }
  table,tr{;width: 100%}
  table{margin-top: 14px;border-right: 1px solid #e6ecec;color:#8d949a}
  td{height: 50px;border-top:1px solid #e6ecec;border-bottom: 1px solid #f0f6f6;border-left: 1px solid #e6ecec}
  .td1{width: 295px}
  .td2{width: 480px}
  .td3{width: 86px}
  td >div{padding-left: 19px}
  .td2-pro{padding: 0 19px}
  .progress {
    height: 20px;
    background: #ebebeb;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 10px;
  }
  .progress > span {
    position: relative;
    float: left;
    margin: 0 -1px;
    min-width: 30px;
    height: 18px;
    line-height: 16px;
    text-align: right;
    background: #cccccc;
    border: 1px solid;
    border-color: #bfbfbf #b3b3b3 #9e9e9e;
    border-radius: 10px;
    background-image: -webkit-linear-gradient(top, #f0f0f0 0%, #dbdbdb 70%, #cccccc 100%);
    background-image: -moz-linear-gradient(top, #f0f0f0 0%, #dbdbdb 70%, #cccccc 100%);
    background-image: -o-linear-gradient(top, #f0f0f0 0%, #dbdbdb 70%, #cccccc 100%);
    background-image: linear-gradient(to bottom, #f0f0f0 0%, #dbdbdb 70%, #cccccc 100%);
    -webkit-box-shadow: inset 0 1px rgba(255, 255, 255, 0.3), 0 1px 2px rgba(0, 0, 0, 0.2);
    box-shadow: inset 0 1px rgba(255, 255, 255, 0.3), 0 1px 2px rgba(0, 0, 0, 0.2);
  }
  .progress > span > span {
    padding: 0 8px;
    font-size: 11px;
    font-weight: bold;
    color: #404040;
    color: rgba(0, 0, 0, 0.7);
    text-shadow: 0 1px rgba(255, 255, 255, 0.4);
  }
  .progress > span:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    height: 18px;
    border-radius: 10px;
  }
  .progress .green {
    background: #61c5bd;
    border-color: #61c5bd;
  }
  .progress .red {
    background: #f4c908
  }
  .progress .orange {
    background: #fc7244;
  }
  .progress .blue {
    background: #38a7de;
  }

</style>



