<template>
  <div>
    <Navbar></Navbar>
    <div class="container">
      <ul>
        <li v-for="list in lists" @click="changeid(list.id)">
            <img :src="url+list.tp" width="100%" alt="">
            <span>{{ list.flmc }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Navbar from '../common/Navbar.vue'
  import router from '../../router'
  import store from '@/vuex/store'
  export  default {
    data(){
      return {
        lists:[],
        url:store.state.url,
        id:''
      }
    },
    components:{
      Navbar
    },
    store,
    methods:{
      creat(){
        var self=this
        $.ajax({
          type: 'get',
          url: self.url + 'resources/zwbkFl/simpleList',
          dataType: 'jsonp',
          success: function (data) {
            console.log(data)
            self.lists=data.data
          }
        })
      },
      changeid(id){
        this.$store.state.zhanyuanid=id
        console.log(id)
        router.push('/zhiwuxiangqinglei')
      }
    },
    created(){
        this.creat()
    }
  }
</script>
<style>
  .container{margin-top: 40px;    height: 900px;
    width: 864px;overflow:scroll}
</style>
<style scoped>
  ul{width: 100%}
  ul li{width: 17%;padding: 1%}
  ul li span{display: block;text-align: center;color:#969696;font-size: 16px;line-height: 28px}
</style>



